.header {
  width: 880px;
  margin: 45px 0 40px;
  padding: 0;
  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media screen and (max-width: 880px) {
  .header {
    width: 100%;
    margin: 28px 0 40px;
  }
}
